body {
  font: 15px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

h1 {
  text-align: center;
}

ol,
ul {
  padding-left: 30px;
}

.game-board {
  margin: 0 auto;
  max-width: 400px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 4rem;
  font-weight: bold;
  line-height: 34px;
  height: 100px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 100px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
}
